import { Components } from '@ionic/core';
import { defineCustomElement as defineCustomElementProgressBar } from '@ionic/core/components/ion-progress-bar';
import type { Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';
import {
  TP_COLORS,
  TP_COLORS_MEDSOFT,
  TP_COLORS_RECURSOS,
} from '../../../../../utils/constants/color.constant';
import {
  TpColorAula,
  TpColorMaterial,
  TpColorOrange,
  TpColorProvas,
  TpColorQuestoes,
  TpColorRevalida,
} from '../../../../../utils/enums/color.enum';
import { TpIonProgressBar } from '../../utils/progress-bar.type';

defineCustomElementProgressBar();

const meta: Meta<Components.IonProgressBar & TpIonProgressBar> = {
  title: 'components/Ionic/Progress-bar/Website',
  argTypes: {
    color: {
      control: { type: 'select' },
      options: [...TP_COLORS, ...TP_COLORS_MEDSOFT, ...TP_COLORS_RECURSOS],
      description: `Define a cor do componente.

**OBS**: As cores **'${TpColorAula.Aula}'**, **'${TpColorMaterial.Material}'**, **'${TpColorQuestoes.Questoes}'**, **'${TpColorRevalida.Revalida}'** e **'${TpColorProvas.Provas}'** estão disponiveis apenas no theme **MedSoft**.

**OBS**: A cor **'${TpColorOrange.Orange}'** está disponivel apenas no theme **Recursos**.`,
      table: {
        defaultValue: {
          summary: 'undefined',
        },
        type: {
          summary: [
            ...TP_COLORS,
            ...TP_COLORS_MEDSOFT,
            ...TP_COLORS_RECURSOS,
          ].join('|'),
        },
      },
    },
  },
};

export default meta;

type Story = StoryObj<Components.IonProgressBar & TpIonProgressBar>;

export const DefaultMarkup: Story = {
  render: () => html` <ion-progress-bar value="0.5"></ion-progress-bar> `,
};
DefaultMarkup.argTypes = {
  color: { table: { disable: true } },
};

export const ValueMarkup: Story = {
  render: () => html` <ion-progress-bar value="0.8"></ion-progress-bar>`,
};
ValueMarkup.argTypes = {
  color: { table: { disable: true } },
};

export const ColorsMarkup: Story = {
  render: ({ ...args }) => html`
    <ion-progress-bar value="0.5" color="${args.color}"></ion-progress-bar>
  `,
};
